.header#header {
    padding: 0;
    z-index:2;
    position:relative;
    font-family: "Open Sans";
}

@media (min-width: $(desktop)) {

    .header#header {
        z-index: 102;
    }
}

.header#header div.header-body{
    background: $white;
    border-bottom:10px solid $purple;
    position:relative;
    z-index:3;
}

.header#header div.container {
    padding:30px;
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
                -ms-grid-row-align: center;
            align-items: center;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
        -ms-flex-pack: center;
            justify-content: center;
}

@media (min-width: $(desktop)) {

    .header#header div.container {
        padding: 50px 0;
    }
}

#logo {

    margin: 0.4rem;
    -webkit-box-ordinal-group: 3;
    -webkit-order: 2;
        -ms-flex-order: 2;
            order: 2;
    margin-left: auto;
}

#logo .mobile {
    max-height:75px;
}

@media (min-width: $(desktop)) {

    #logo {
        -webkit-box-ordinal-group: 2;
        -webkit-order: 1;
            -ms-flex-order: 1;
                order: 1;
        margin-left: 0;
    }
}

.menu-trigger#trigger {
    display: block;
    -webkit-box-ordinal-group: 2;
    -webkit-order: 1;
        -ms-flex-order: 1;
            order: 1;
    position: relative;
    padding-left: 40px;
    font-size: 0.9em;

    color:$blue;
    text-decoration:none;
    font-size:24px;
    font-weight:bold;

}

.menu-trigger#trigger .open {
    display:none;
}

.menu-trigger#trigger .open .icon:before {
    float:right;
    margin-top:0px;
    margin-left:10px;
    font-size:34px;
}

.menu-trigger#trigger .closed {
    padding-right:50px;
}

.menu-trigger#trigger .closed:before {
    position: absolute;
    top: 2px;
    width: 30px;
    height: 6px;
    left:0;
    background: #fff;
    box-shadow: 0 5px $blue, 0 7px #fff, 0 12px $blue, 0 14px #fff, 0 19px $blue;
    content: '';
}

@media (min-width: $(desktop)) {

    .menu-trigger#trigger .closed:before {
        right: 0;
        left: auto;
    }
}

.menu-trigger#trigger.open .closed {
    display: none;
}

.menu-trigger#trigger.open .open {
    display: block;
}

@media (min-width: $(desktop)) {

    .menu-trigger#trigger {
        -webkit-box-ordinal-group: 3;
        -webkit-order: 2;
            -ms-flex-order: 2;
                order: 2;
        margin-left: auto;
        padding-left: 0;
    }
}

